import React from 'react';
import "./App.css";
import { Button } from "./Button.js";

function getTime(num){
  // get current time
  let fullTime = new Date();
  let hour = (fullTime.getHours()+num) % 24;
  let minute = fullTime.getMinutes();
  let sec = fullTime.getSeconds();

  // if the digit is single, put 0 before it
  hour = padZero(hour);
  minute = padZero(minute);
  sec = padZero(sec);
  
  return `${hour} : ${minute} : ${sec}`;
}

function padZero(num){
  if(num > 9) return num;
  else return '0' + num;
}

export class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {time: getTime(0)}
  }

  componentDidMount() {
    this.intervalID = setInterval(() => this.setState({time: getTime(0)}), 1000);
  }

  componentWillUnmount() {
    clearInterval(this.intervalID);
  }

  handleButton(hour) {
    clearInterval(this.intervalID);
    this.setState({ time: getTime(hour) });
    this.intervalID = setInterval(() => this.setState({time: getTime(hour)}), 1000); 
  }

  render() {
    return (
      <div className="App">
        <header>
          <h1>World Clock</h1>
        </header>
        <main>
          <div className="btn-collection">
            <Button className="seoul" onClick={() => this.handleButton(0)}>Seoul</Button>
            <Button className="berkeley" onClick={() => this.handleButton(8)}>Berkeley</Button>
            <Button className="new-york" onClick={() => this.handleButton(11)}>New York</Button>
          </div>
          <span className="time">{this.state.time}</span>
        </main>
      </div>
    );
  }
	
}

export default App;
